﻿@mixin media-up($name, $breakpoints: $media-breakpoints) {
    $min-width: map-get($breakpoints, $name);

    @if $min-width {
        @media (min-width: $min-width) {
            @content;
        }
    }
    @else {
        @warn "The Key #{$name} is not present in media-breakpoints map. ";
    }
}
